.modal {
  display: flex;
  border: none;
  padding: 0;
  flex-direction: column;
  border-radius: 16px;
  margin-top: 90px;

  &::backdrop {
    background-color: rgba(13, 29, 51, 0.4);
    width: 100%;
    height: 100%;
  }

  @include vp-1199 {
    margin-top: 120px;
  }

  @include vp-767 {
    margin-top: 24px;
    transform: scale(1.03);
  }
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  border-radius: 16px;

  .feedback__form {
    padding: 60px 80px 58px;
    background-color: $color-default-white;
    color: $color-charcoal;
    display: flex;
    flex-direction: column;
    max-width: 510px;
    gap: 18px 0;

    @include vp-767 {
      gap: 15px 0;
      padding: 60px 10px 58px;
    }

    input,
    .custom-select__button,
    .custom-toggle__icon {
      border-color: rgba(72, 72, 72, 0.3);
    }

    input,
    .custom-select__button {
      color: $color-charcoal;
    }
  }

  .custom-toggle input:checked + .custom-toggle__icon {
    background-color: $color-cerulean-blue;

    &::before {
      background-image: url('data:image/svg+xml,\<svg width="10" height="8" viewBox="0 0 10 8" fill="none" xmlns="http://www.w3.org/2000/svg">\<path d="M1 3.5L3.94737 7L9 1" stroke="white" stroke-linecap="round" stroke-linejoin="round"/>\</svg>');
    }
  }

  .custom-select__icon {
    background-color: $color-charcoal;
  }

  .custom-textarea {
    display: none;
  }

  .btn--secondary::before {
    background-color: $color-cerulean-blue;
  }

  .feedback__title {
    margin: 0 0 7px;
    font-weight: 400;
    font-size: 32px;
    line-height: 36px;
    text-align: center;
    color: $color-cerulean-blue;

    @include vp-1199 {
      margin: 0 0 13px;
      font-size: 28px;
      line-height: 28px;
    }

    @include vp-767 {
      margin: 0 0 8px;
      font-size: 18px;
      line-height: 18px;
    }
  }

  .feedback__text {
    margin: 0 0 21px;
    text-align: center;

    @include vp-1199 {
      margin: 0 0 24px;
      line-height: 20px;
    }

    @include vp-767 {
      align-self: center;
      max-width: 90%;
      margin: 0 0 20px;
      font-size: 14px;
    }
  }

  .feedback__btn {
    width: 60%;
    align-self: center;
    margin: 12px 0 0;
    color: $color-cerulean-blue;
    border: 1px solid $color-cerulean-blue;

    svg {
      color: $color-default-white;
    }

    @include vp-767 {
      align-self: stretch;
      margin: 9px 0 0;
      width: 100%;
    }
  }
}

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;
  color: $color-cerulean-blue;
  background: $color-transparent;
  border: none;
  outline: none;
  cursor: pointer;
  transition:
    opacity $trans-default,
    color $trans-default;

  @include vp-767 {
    top: 15px;
    right: 16px;
    width: 25px;
    height: 25px;

    svg {
      width: 16px;
      height: 16px;
    }
  }

  @include hover-focus {
    opacity: 0.7;
  }

  @include active {
    color: $color-endeavour;
    opacity: 1;
  }
}
